<template>
  <section class="trading-tables">
    <div class="trading-tables__nav">
      <div class="toggle-box trading-tables__toggle-box">
        <button class="toggle-box__button" :class="{'toggle-box__button--active': mainTables.openOrders}" @click="chooseMainTable('openOrders')">Open orders</button>
        <button class="toggle-box__button" :class="{'toggle-box__button--active': mainTables.balance}" @click="chooseMainTable('balance')">Balance</button>
        <button class="toggle-box__button" :class="{'toggle-box__button--active': mainTables.positions}" @click="chooseMainTable('positions')">Positions</button>
        <button class="toggle-box__button" :class="{'toggle-box__button--active': mainTables.tradingHistory}" @click="chooseMainTable('tradingHistory')">Trade history</button>
        <button class="toggle-box__button" :class="{'toggle-box__button--active': mainTables.pricingAlerts}" @click="chooseMainTable('pricingAlerts')">Pricing alerts</button>
      </div>
      <label class="search trading-tables__search">
        <icon-search class="search__img"/>
        <input type="text" class="search__input" placeholder="Search...">
      </label>
    </div>
    <div class="trading-tables__field">
      <transition
		name="custom-classes-transition"
		enter-active-class="animated dur03 fadeIn"
		leave-active-class="animated dur03 fadeOut"
		mode="out-in">
		<trading-open-orders-table v-if="mainTables.openOrders"/>
		<trading-balance-table v-else-if="mainTables.balance"/>
    <trading-positions-table v-else-if="mainTables.positions"/>
		<trading-history-table v-else-if="mainTables.tradingHistory"/>
		<trading-pricing-alerts-table v-else/>
      </transition>
    </div>
  </section>
</template>
<script>
import TradingPositionsTable from "@/components/trading/tables/TradingPositionsTable.vue"
import TradingOpenOrdersTable from "@/components/trading/tables/TradingOpenOrdersTable.vue"
import TradingBalanceTable from "@/components/trading/tables/TradingBalanceTable.vue"
import TradingHistoryTable from "@/components/trading/tables/TradingHistoryTable.vue"
import TradingPricingAlertsTable from "@/components/trading/tables/TradingPricingAlertsTable.vue"
import IconSearch from "@/components/icons/IconSearch.vue"

export default {
  name: "TradingTables",
  components: {
    TradingPositionsTable,
    TradingOpenOrdersTable,
    TradingBalanceTable,
    TradingHistoryTable,
    TradingPricingAlertsTable,
    IconSearch
  },
  data() {
    return {
      mainTables: {
        openOrders: true,
        balance: false,
        positions: false,
        tradingHistory: false,
        pricingAlerts: false
      }
    };
  },
  methods: {
    chooseMainTable(table) {
      let tables = this.mainTables;
      for (let el in tables) {
        el == table ? tables[el] = true: tables[el] = false;
      }
    }
  }
};
</script>
<style lang="scss">
.trading-tables {
  @extend %card;
  @include flex-col-def(stretch);
	padding: 24px;
  &__nav {
    @include flex-row(space-between);
    padding-bottom: 20px;
  }
  &__toggle-box {
    margin-right: 1rem;
  }
  &__field {
    flex: 0 1 100%;
    padding: 0 1rem 0 0;
    @include flex-col-def(stretch);
  }
}
</style>