<template>
<table class="trading-favorites-table">
	<thead class="trading-favorites-table__thead">
		<tr class="trading-favorites-table__row">
			<th class="trading-favorites-table__cell trading-favorites-table__cell--pair trading-favorites-table__cell--head ">Pair
				<icon-arrow-up class='trading-favorites-table__head-img'/>
			</th>
			<th class="trading-favorites-table__cell trading-favorites-table__cell--price trading-favorites-table__cell--head ">price
				<icon-arrow-up class='trading-favorites-table__head-img'/>
			</th>
			<th class="trading-favorites-table__cell trading-favorites-table__cell--hours trading-favorites-table__cell--head">24h
				<icon-arrow-up class='trading-favorites-table__head-img'/>
			</th>
		</tr>
	</thead>
	<tbody class="trading-favorites-table__tbody">
		<tr class="trading-favorites-table__row" v-for='favorite in favorites' :key='favorite.id'>
			<td class="trading-favorites-table__cell trading-favorites-table__cell--pair trading-favorites-table__cell--star" @click="star(favorite.id)">
				<icon-star class="trading-favorites-table__star"/>
				{{ favorite.pair }}
			</td>
			<td class="trading-favorites-table__cell trading-favorites-table__cell--price">{{ favorite.price }}</td>
			<td class="trading-favorites-table__cell trading-favorites-table__cell--hours">{{ favorite.hours }}</td>
		</tr>
	</tbody>
</table>
</template>
<script>
import IconArrowUp from '@/components/icons/IconArrowUp.vue'
import IconStar from '@/components/icons/IconStar.vue'

export default {
	name: 'TradingFavoritesTable',
	components: {
		IconArrowUp,
		IconStar
	},
	data () {
		return {
			favorites: []
		}
	},
	methods: {
		pushFavorites() {
			for (let i = 0; i < 25; i++) {
				this.favorites.push({
					pair: "BCH / BTC",
					price: "2.735",
					hours: "+1.37%",
					id: i
				})
			}				
		}
	},
	created() {
		this.pushFavorites()
	}
}
</script>
<style lang="scss">

.trading-favorites-table {
	@include flex-col-def(stretch);
	border: none;
	overflow-y: auto;
	padding: 10px 0;
	&__star {
		fill: $star-color-active;
		widows: 13px;
		height: 13px;
		&--active {
			filter: brightness(1.1)
		}
	}
	&__thead {
		@include flex-col(stretch);
		align-items: stretch;
		padding-right: 8px;
		padding-bottom: 10px;
	}
	&__head-img {
		margin-left: 4px;
		fill: $lightest-grey;
		width: 9px;
		height: 9px;
	}
	&__tbody {
		padding-right: 4px;
		@include flex-col-def(stretch);
		overflow-y: auto;
		&::-webkit-scrollbar {
			width: 4px;
			height: 4px;
		}
		&::-webkit-scrollbar-thumb {
			width: 4px;
			border-radius: 10px;
			background: $scroll-color;
		}
		&::-webkit-scrollbar-track {
			background: transparent;
		}
	}
	&__row {
		flex: 1 0 100%;
		@include flex-row(space-between);
		padding-bottom: 12px;
	}
	&__cell {
		font-family: $second-font;
		color: $dark-grey;
		font-size: $size-3;
		text-transform: uppercase;
		font-weight: 600;
		flex: 0 0 33.3%;
		padding-right: 0.5rem;
		&--head {
			font-family: $base-font;
			color: $middle-grey;
			font-size: $size-2;
			text-transform: uppercase;
			font-weight: 600;
			@include flex-row-def(baseline);
			cursor: pointer;
			&:active  .trading-favorites-table__head-img {
				fill: $text-hover-color;
			}
		}
		&--star {
			cursor: pointer;
		}
		&--pair {
			flex-basis: 35%;
		}
		&--price {
			flex-basis: 40%;
		}
		&--hours {
			justify-content: flex-end;
			text-align: right;
			flex-basis: 25%;
		}
	}
}


</style>