<template>
<table class="dashboard-list-table">
	<thead class="dashboard-list-table__thead">
		<tr class="dashboard-list-table__row dashboard-list-table__row--head">
			<th class="dashboard-list-table__cell dashboard-list-table__cell--head dashboard-list-table__cell--coin">
				<span class='dashboard-trades-table__text'>coin</span>
				<icon-angle-down class='dashboard-list-table__head-img'/>		
			</th>
			<th class="dashboard-list-table__cell dashboard-list-table__cell--head">
				<span class='dashboard-trades-table__text'>exchange</span>
				<icon-angle-down class='dashboard-list-table__head-img'/>	
			</th>
			<th class="dashboard-list-table__cell dashboard-list-table__cell--right dashboard-list-table__cell--head">
				<span class='dashboard-trades-table__text'>price</span>
				<icon-angle-down class='dashboard-list-table__head-img'/>	
			</th>
			<th class="dashboard-list-table__cell dashboard-list-table__cell--right dashboard-list-table__cell--head">
				<span class='dashboard-trades-table__text'>day</span>
				<icon-angle-down class='dashboard-list-table__head-img'/>	
			</th>
			<th class="dashboard-list-table__cell dashboard-list-table__cell--head">
				<span class='dashboard-trades-table__text'>graph</span>
			</th>
			<th class="dashboard-list-table__cell dashboard-list-table__cell--head"></th>
		</tr>
	</thead>
	<tbody class="dashboard-list-table__tbody">
		<tr class="dashboard-list-table__row" v-for="n in 40" :key='n.id'>
			<td class="dashboard-list-table__cell">BTC_EUR</td>
			<td class="dashboard-list-table__cell">livecoin</td>
			<td class="dashboard-list-table__cell dashboard-list-table__cell--right dashboard-list-table__cell--price">3255,04</td>
			<td class="dashboard-list-table__cell dashboard-list-table__cell--right dashboard-list-table__cell--day">-4.15%</td>
			<td class="dashboard-list-table__cell">
				<img src="@/assets/images/mini_graph.jpg" alt="graph">
			</td>
			<td class="dashboard-list-table__cell">
				<button class="dashboard-list-table__delete">
					<icon-trash class="dashboard-list-table__delete-img"/>
				</button>
			</td>
		</tr>	
	</tbody>
</table>
</template>
<script>
import IconAngleDown from '@/components/icons/IconAngleDown.vue'
import IconTrash from '@/components/icons/IconTrash.vue'

export default {
	name: 'DashboardListTable.vue',
	components: {
		IconAngleDown,
		IconTrash
	},
	data () {
		return {
			balance: [],
			balanceTotal: 0
		}
	},
	methods: {
		pushBalance() {
			let data = {
				currency: "",
				total: "",
				rate: ""
			}
			for(let i = 0; i < 1; i++) {
				this.balance.push(data)
			}
		}
	},
	created() {
		this.pushBalance()
	}
}
</script>
<style lang="scss">
.dashboard-list-table {
	border: none;
	overflow-y: auto;
	@include flex-col-def(stretch);
	&__head-img {
		margin-left: 4px;
		fill: $lightest-grey;
		width: 9px;
		height: 9px;
	}
	&__thead {
		@extend %table-head;
		padding-bottom: 0px;
	}
	&__tbody {
		@extend %table-body;
	}
	&__row {
		display: grid;
		grid-template-columns: repeat(5, 2fr) 1fr;
		justify-items: start;
		justify-content: space-between;
		align-items: center;
		grid-column-gap: 32px;
		padding: 0 6px 6px 0;
		&--head {
			padding-bottom: 14px;
		}
	}
	&__text {
		padding-right: 6px;
	}
	&__cell {
		@extend %table-cell;
		color: $darker-grey;
		&--head {
			cursor: pointer;
				color: $middle-grey;
		}
		&--right {
			justify-self: end;
		}
		&--price {
			color: $text-hover-color;
		}
		&--day {
			color: $text-sell-color;
		}
	}
	&__delete {
		background-color: transparent;
		width: 24px;
		height: 24px;
		@include flex-row(center);
		justify-self: end;
		transition: 0.2s ease-in-out;
		@media (min-width: 480px) {
			&:hover {
				opacity: 0.95;
			}
		}
	}
	&__delete-img {
		width: 14px;
		height: 14px;
		fill: $text-hover-color;
		pointer-events: none;
		transition: 0.2s ease-in-out;
	}
}


</style>