<script>
import IconAngleDown from '@/components/icons/IconAngleDown.vue'

export default {
	name: 'DashboardHoldingTable.vue',
	components: {
		IconAngleDown
	},
	data () {
		return {
			balance: [],
			balanceTotal: 0
		}
	},
	methods: {
		pushBalance() {
			let data = {
				currency: "",
				total: "",
				rate: ""
			}
			for(let i = 0; i < 1; i++) {
				this.balance.push(data)
			}
		}
	},
	created() {
		this.pushBalance()
	}
}
