<template>
<div class="portfolio">
	<div class="portfolio__box">
		<div class="portfolio__head">		
			<h1 class="portfolio__title">Portfolio</h1>			
			<div class="portfolio__head-inner">
				<input type="date" class="portfolio__date-input portfolio__date-input--margin">
				<input type="date" class="portfolio__date-input">
			</div>	
		</div>
		<ul class="portfolio__table-row portfolio__table-row--head">
			<li class="portfolio__table-cell portfolio__table-cell--head">
				<p class="portfolio__table-text">bct</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
			<li class="portfolio__table-cell portfolio__table-cell--head portfolio__table-cell--end">
				<p class="portfolio__table-text">amount</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
			<li class="portfolio__table-cell portfolio__table-cell--head">
				<p class="portfolio__table-text">price, usd</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
			<li class="portfolio__table-cell portfolio__table-cell--head portfolio__table-cell--end">
				<p class="portfolio__table-text">day</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
			<li class="portfolio__table-cell portfolio__table-cell--head portfolio__table-cell--end">
				<p class="portfolio__table-text">value, usd</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
			<li class="portfolio__table-cell portfolio__table-cell--head portfolio__table-cell--end">
				<p class="portfolio__table-text">%</p>
				<icon-angle-down class='portfolio__angle-img'/>
			</li>
		</ul>
		<article class="portfolio__element" v-for='item in tokenBox' :key='item.id'>
			<ul class="portfolio__table-row" :class="{'portfolio__table-row--window':(item.deposit || item.withdraw)}">
				<li class="portfolio__table-cell">{{ item.coin }}</li>
				<li class="portfolio__table-cell portfolio__table-cell--end">{{ item.amount }}</li>
				<li class="portfolio__table-cell">{{ item.price }} USD</li>
				<li class="portfolio__table-cell portfolio__table-cell--end">{{ item.day }}</li>
				<li class="portfolio__table-cell portfolio__table-cell--end">{{ item.value }} USD</li>
				<li class="portfolio__table-cell portfolio__table-cell--end portfolio__table-cell--percent">{{ item.percent }}%</li>
				<li class="portfolio__table-cell portfolio__table-cell--end portfolio__table-cell--line">
					<button class="portfolio__button portfolio__button--deposit"
									@click='showDeposit(item.id)'>Deposit
					</button>
				</li>
				<li class="portfolio__table-cell portfolio__table-cell--end portfolio__table-cell--line">
					<button class="portfolio__button portfolio__button--withdraw"
									@click='showWithdraw(item.id)'>Withdraw
					</button>
				</li>
			</ul>
			<transition
				name="custom-classes-transition"
				enter-active-class="animated dur02 pullDown"
				mode="out-in">
			<section class="deposit-window" v-if="item.deposit" key='deposit'>
				<p class="deposit-window__caption">Your Bitcoin Deposit Address</p>
				<div class="deposit-window__inner">
					<p class="deposit-window__address">1BvBMSEYstWetqTFn5Au4m4GFg7xJaNVN2</p>
					<button class="deposit-window__button" :class='{"button-loading": item.loading}' @click='loading(item.id)'>Copy</button>
				</div>
				<p class="deposit-window__information">IMPORTANT: Send only BTC to this deposit address </p>
				<div class="deposit-window__line"></div>
			</section>
			<section class="withdraw-window" v-else-if="item.withdraw" key='withdraw'>
				<p class="withdraw-window__caption">Your have 0.000400906 BTC available for withdrawal</p>
				<div class="withdraw-window__inner">
					<label class="withdraw-window__label">
						<p class="withdraw-window__input-name">Address</p>
						<input type="text" class="withdraw-window__input">
					</label>
					<label class="withdraw-window__label">
						<p class="withdraw-window__input-name">Amount</p>
						<input type="text" class="withdraw-window__input">
					</label>
					<button class="withdraw-window__button" :class='{"button-loading": item.loading}' @click='loading(item.id)'>Withdraw</button>
					<p class="withdraw-window__text withdraw-window__text--transaction">Transaction Fee:</p>
					<p class="withdraw-window__text withdraw-window__text--total">Total:</p>
				</div>
				<div class="withdraw-window__line"></div>
			</section>
			</transition>
			<section class="portfolio__withdraw"></section>
		</article>
	</div>
	<div class="portfolio__box portfolio-status">
		<p class="portfolio-status__heading">HOLDINGS</p>
		<p class="portfolio-status__heading">CHANGE 7d</p>
		<p class="portfolio-status__heading">ORIGINAL COST</p>
		<p class="portfolio-status__heading">REALIZED P/L</p>
		<p class="portfolio-status__text">$0</p>
		<p class="portfolio-status__text portfolio-status__text--green">$0(0.00%)</p>
		<p class="portfolio-status__text">$0</p>
		<p class="portfolio-status__text portfolio-status__text--green">$0</p>
	</div>
	<div class="portfolio__box portfolio-graph">
		<div class="portfolio__title-wrapper portfolio-graph__header">
			<h1 class="portfolio__title">Portfolio</h1>
			<icon-question class="portfolio__question"/>
		</div>
		<div class="portfolio-graph__field"></div>
		<div class="portfolio-graph__value-box">
			<p class="portfolio-graph__value" v-for='n in 6' :key='n.id'>{{260 - 20 * n}},000</p>
		</div>
		<div class="portfolio-graph__date-box">
			<p class="portfolio-graph__date">21 Oct</p>
			<p class="portfolio-graph__date">28 Oct</p>
			<p class="portfolio-graph__date">04 Oct</p>
			<p class="portfolio-graph__date">11 Oct</p>
			<p class="portfolio-graph__date">18 Oct</p>
			<p class="portfolio-graph__date">25 Oct</p>
			<p class="portfolio-graph__date">Dev</p>
			<p class="portfolio-graph__date">09 Dev</p>
		</div>
	</div>
</div>
</template>
<script>
import IconAngleDown from "@/components/icons/IconAngleDown.vue"
import IconQuestion from '@/components/icons/IconQuestion.vue'

export default {
	name: "Portfolio",
	components: {
		IconAngleDown,
		IconQuestion
	},
	data () {
		return {
			tokenBox: [
			]
		}
	},
	methods: {
		pushBox() {
			for (let i = 0; i < 10; i++) {
				this.tokenBox.push({
				coin: "btc",
				amount: "2",
				price: "3 498,34",
				day: "04.11.2018",
				value: "3 498,34",
				percent: "+10.25",
				deposit: false,
				withdraw: false,
				loading: false,
				id: i
			});
			}
		},
		showDeposit(index) {
			let item = this.tokenBox[index];
			item.withdraw = false;
			item.deposit = !item.deposit;
		},
		showWithdraw(index) {
			let item = this.tokenBox[index];
			item.deposit = false;
			item.withdraw = !item.withdraw;
		},
		loading(index) {
			let item = this.tokenBox[index];
			item.loading = !item.loading;
		}
	},
	created() {
		this.pushBox();
	}
}
</script>

<style lang="scss" scoped>
.portfolio {
	padding: 0 20%;
	&__box {
		@extend %box;
		padding: 0 0 25px;
		margin-bottom: 16px;
	}
	&__question {
		width: 20px;
		height: 20px;
		fill: $lightest-grey;
		cursor: pointer;
		transition: fill ease-in-out 0.15s;
		background-color: transparent;
		@include flex-row(center);
		@media (min-width: 480px) {
			&:hover {
				fill: $light-grey;
			}
		}
	}
	&__title-wrapper {
		@include flex-row-def(center);
	}
	&__title {
		margin-right: 12px;
	}
	&__head {
		@include flex-row(space-between);
		align-items: flex-start;
		padding: 25px 32px;
	}
	&__head-inner {
		@include flex-row(center);
		flex-wrap: nowrap;
	}
	&__date-input {
		@extend %input;
		height: auto;
		padding: 9.5px 12px;
		color: $light-grey;
		&--margin {
			margin-right: 24px;
		}
	}
	&__angle-img {
		fill: $semidark-grey;
		pointer-events: none;
		width: 8px;
		height: 8px;
	}
	&__element:last-of-type {
			.portfolio__table-cell {
				padding-bottom: 0;
			}
	}
	&__table-row {
		list-style: none;
		@include flex-row-def(center);
		display: grid;
		grid-template-columns: repeat(8, 1fr);
		justify-items: start;
		grid-column-gap: 24px;
		padding: 0 32px;
		&--head {
			padding: 0 32px 19px;
		}
		&--window {
			padding-bottom: 18px;
			position: relative;
			.portfolio__table-cell {
				padding-bottom: 0;
			}
		}
	}
	&__table-cell {
		font-size: $size-2;
		color: $darker-grey;
		font-weight: 600;
		letter-spacing: 0.28px;
		text-transform: uppercase;
		white-space: nowrap;
		padding-bottom: 18px;
		&--head {
			color: $middle-grey;
			cursor: pointer;
			@include flex-row-def(center);
			padding-bottom: 0;
		}
		&--end {
			justify-self: end;
		}
		&--percent {
			color: $btn-color-buy;
		}
		&--line {
			text-align: right;
			justify-self: stretch;
			border-left: 1px solid $form-bg-color;
		}
	}
	&__table-text {
		white-space: nowrap;
		color: $middle-grey;
		margin-right: 5px;
	}
	&__button {
		background-color: transparent;
		text-transform: capitalize;
		font-weight: 600;
		font-size: $size-3;
		&--deposit {
			color: $text-hover-color;
		}
		&--withdraw {
			color: $btn-color-buy;
		}
	}
}
.deposit-window {
	padding: 18px 0 0;
	background-color: $bg-color;
	&__line {
		height: 18px;
		background-color: $white;
	}
	&__caption {
		padding-left: 32px;
		color: $text-active-color;
		font-size: $size-3;
		font-weight: 600;
		margin-bottom: 10px;
	}
	&__address {
		min-width: 350px;
		@extend %input;
		background-color: $white;
		color: $light-grey;
		margin-right: 12px;
	}
	&__inner {
		@include flex-row-def(stretch);
		height: 36px;
		margin-bottom: 10px;
		padding-left: 32px;
	}
	&__button {
		@extend %button-buy;
		text-transform: capitalize;
		font-size: $size-4;
		padding: 8px 32px;
	}
	&__information {
		color: $text-scpecial-color;
		font-size: $size-3;
		font-weight: 600;
		margin-bottom: 25px;
		padding-left: 32px;
	}
}
.withdraw-window {
	padding: 18px 0 0;
	background-color: $bg-color;
	&__line {
		height: 18px;
		background-color: $white;
	}
	&__caption {
		padding-left: 32px;
		color: $middle-grey;
		font-size: $size-3;
		font-weight: 600;
		margin-bottom: 16px;
	}
	&__input-name {
		color: $text-active-color;
		font-size: $size-3;
		font-weight: 600;
		margin-bottom: 10px;
	}
	&__input {
		min-width: 200px;
		@extend %input;
		width: 100%;
	}
	&__inner {
		width: 90%;
		max-width: 800px;
		display: grid;
		grid-template-columns: 2fr 2fr 1fr;
		grid-template-rows: repeat(3, auto);
		grid-column-gap: 32px;
		grid-row-gap: 16px;
		margin-bottom: 18px;
		padding: 0 32px;
	}
	&__button {
		align-self: end;
		@extend %button-buy;
		text-transform: capitalize;
		font-size: $size-4;
		height: 36px;
		padding: 8px 32px;
	}
	&__text {
		color: $semidark-grey;
		font-size: $size-5;
		font-weight: 600;
		&--transaction {
			grid-area: 2 / 2 / 3 / 3;
		}
		&--total {
			grid-area: 3 / 2 / 4 / 3;
		}
	}
}
.portfolio-status {
	padding: 25px 32px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(2, auto);
	grid-gap: 8px;
	justify-items: center;
	&__heading {
		font-size: $size-1;
		font-weight: 500;
		color: $middle-grey;
	}
	&__text {
		color: $light-grey;
		font-size: $size-4;
		font-weight: 600;
		&--green {
			color: $btn-color-buy;
		}
	}
}
.portfolio-graph {
	padding: 25px 32px;
	display: grid;
	grid-template-columns: 8fr 80px;
	grid-template-rows: repeat(3,auto);
	grid-row-gap: 9px;
	grid-template-areas: 
		'header header'
		'graph value'
		'date .';
	&__header {
		grid-area: header;
		padding-bottom: 10px;
	}
	&__field {
		background-image: url('~@/assets/images/Clipboard01.jpg');
		@extend %bg-image;
		min-height: 240px;
		grid-area: graph;
	}
	&__value-box {
		padding-left: 18px;
		grid-area: value;
		display: grid;
		align-items: end;
		justify-content: space-between;
		grid-template-rows: repeat(6, auto);
	}
	&__value {
		padding-bottom: 8px;
	}
	&__date-box {
		grid-area: date;
		display: grid;
		grid-template-columns: repeat(8, auto);
		justify-content: space-between;
	}
	&__date,
	&__value {
		font-size: $size-1;
		color: $middle-grey;
		font-weight: 500;
		letter-spacing: -0.2px;
	}
}
</style>
