<template>
<table class="trading-balance-table">
	<thead class="trading-balance-table__thead">
		<tr class="trading-balance-table__row">
			<th class="trading-balance-table__cell trading-balance-table__cell--head trading-balance-table__cell--currency">currency
				<icon-arrow-up class='trading-balance-table__head-img'/>		
			</th>
			<th class="trading-balance-table__cell trading-balance-table__cell--head trading-balance-table__cell--total">total
				<icon-arrow-up class='trading-balance-table__head-img'/>
			</th>
			<th class="trading-balance-table__cell trading-balance-table__cell--head trading-balance-table__cell--rate">rate
				<icon-arrow-up class='trading-balance-table__head-img'/>
			</th>
			<th class="trading-balance-table__cell trading-balance-table__cell--head trading-balance-table__cell--balance">
				Balance total:  {{ balanceTotal }} USD
			</th>
		</tr>
	</thead>
	<tbody class="trading-balance-table__tbody">
		<tr class="trading-balance-table__row" v-for='item in balance' :key='item.id'>
			<td class="trading-balance-table__cell trading-balance-table__cell--currency">{{ item.currency }}</td>
			<td class="trading-balance-table__cell trading-balance-table__cell--total">{{ item.total }}</td>
			<td class="trading-balance-table__cell trading-balance-table__cell--rate">{{ item.rate }}</td>
			<td class="trading-balance-table__cell trading-balance-table__cell--balance"></td>
		</tr>	
	</tbody>
</table>
</template>
<script>
import IconArrowUp from '@/components/icons/IconArrowUp.vue'

export default {
	name: 'TradingBalanceTable',
	components: {
		IconArrowUp
	},
	data () {
		return {
			balance: [],
			balanceTotal: 0
		}
	},
	methods: {
		pushBalance() {
			let data = {
				currency: "",
				total: "",
				rate: ""
			}
			for(let i = 0; i < 1; i++) {
				this.balance.push(data)
			}
		}
	},
	created() {
		this.pushBalance()
	}
}
</script>
<style lang="scss">
.trading-balance-table {
	border: none;
	overflow-y: auto;
	@include flex-col-def(stretch);
	&__head-img {
		margin-left: 4px;
		fill: $lightest-grey;
		width: 9px;
		height: 9px;
	}
	&__thead {
		@extend %table-head;
	}
	&__tbody {
		@extend %table-body;
	}
	&__row {
		@extend %table-row;
	}
	
	&__cell {
		flex: 1 1 11.1%;
		padding-right: 0.5rem;
		@extend %table-cell;
		&--currency {
			flex-basis: 15%;
		}
		&--total {
			flex-basis: 10%;
		}
		&--rate {
			flex-basis: 10%;
		}
		&--balance {
			color: $grey;
			font-weight: 500;
			justify-content: center;
			flex-basis: 55%;
			text-transform: none;
		}
	}
}


</style>
