<template>
<div class="exchange" @click.self="visible.dropMenu = false">
	<transition
		name="custom-classes-transition"
		enter-active-class="animated dur04 bounceInUp">
	<dir class="exchange__box exchange__box--store exchange__box--margin" @click="visible.dropMenu = false" v-show="apiStore.length">
		<div class="exchange__title-wrapper">
			<h1 class="exchange__title">Your API Keys</h1>
			<icon-question class="exchange__question"/>
		</div>
		<div class="exchange__header">
			<p class="exchange__heading">exchange</p>
			<p class="exchange__heading">api key</p>
			<p class="exchange__heading exchange__heading--balance">balance</p>
			<p class="exchange__heading exchange__heading--status">status</p>
		</div>
		<div class="api-item" v-for='(item, index) in apiStore' :key='item.id'>
			<div class="api-item__name-wrapper">
				<div class="api-item__name-img"></div>
				<p class="api-item__name">{{ item.name }}</p>
			</div>
			<p class="exchange__api">{{ item.api }}</p>
			<p class="api-item__balance">{{ item.balance }} USD</p>		
			<label class="api-item__status">
				<input class="api-item__status-input" name='status' type="checkbox"/>
					<div class="api-item__status-dot-wrapper">
						<div class="api-item__status-dot"></div>
					</div>
			</label>
			<button class="api-item__delete" @click='deleteApi(index)'>
				<icon-trash class="api-item__delete-img"/>
			</button>
		</div>
	</dir>
	</transition>
	<div class="exchange__box" @click.self="visible.dropMenu = false">
		<div class="exchange__box-inner" @click.self="visible.dropMenu = false">
			<div class="exchange__title-wrapper exchange__title-wrapper--avilible" @click.self="visible.dropMenu = false">
				<h1 class="exchange__title">Avialible Exchanges</h1>
				<icon-question class="exchange__question"/>
			</div>
			<div class="drop-menu" @click.self="visible.dropMenu = !visible.dropMenu">
				<p class="drop-menu__placeholder" :class="{'drop-menu__item-name':dropList.select}">{{ dropList.select?dropList.select:"Choose an exchange"}}</p>
				<div class="drop-menu__list" v-show='visible.dropMenu'>
					<div class="drop-menu__item" v-for='item in dropList.box' :key='item.id' @click="selectDropItem(item.name)">
						<div class="drop-menu__item-img"></div>
						<p class="drop-menu__item-name">{{ item.name }}</p>
					</div>
				</div>
			</div>
		</div>
		<div class="exchange__header exchange__header--avilible" @click.self="visible.dropMenu = false">
			<p class="exchange__heading">exchange</p>
			<p class="exchange__heading">api key</p>
		</div>
		<div class="exchange__field">
			<div class="api-box" v-for='(item, index) in apiBox' :key='item.id'>
				<div class="api-box__name-wrapper">
					<div class="api-box__name-img"></div>
					<p class="api-box__name">{{ item.name }}</p>
				</div>
				<input class='api-box__input api-box__input--api' 
					type="text" 
					placeholder="API Key"
					v-model.trim="item.api">
				<input class='api-box__input api-box__input--key' 
					type="text" 
					placeholder="Secret Key"
					v-model.trim="item.key">
				<button class="api-box__button-save" 
					:disabled='!(item.api && item.key)'
					@click='pushApi(index)'>Save</button>
				<button class="api-box__question">
					<icon-question class='api-box__question-img'/>
				</button>
				<button class="api-box__create-button">Create an account</button>
			</div>
		</div>
	</div>
</div>
</template>
<script>
import IconQuestion from '@/components/icons/IconQuestion.vue'
import IconTrash from '@/components/icons/IconTrash.vue'

export default {
	name: "MyExchanges",
	components: {
		IconQuestion,
		IconTrash
	},
	data () {
		return {
			visible: {
				dropMenu: false
			},
			dropList: {
				box: [
					{name: "Bibox"},
					{name: "Binance"},
					{name: "Bit-Z"},
					{name: "Bitfinex"},
					{name: "Poloniex"},
					{name: "Bibox"},
					{name: "Binance"},
					{name: "Bit-Z"},
					{name: "Bitfinex"},
					{name: "Poloniex"}
				],
				select: ""
			},
			apiBox: [
				{name: "Bibox",	api: '', key: '', balance: '0.18'},
				{name: "Binance",	api: '', key: '', balance: '0.18'},
				{name: "Bit-Z",	api: '', key: '', balance: '0.18'},
				{name: "Bitfinex",	api: '', key: '', balance: '0.18'},
				{name: "Poloniex",	api: '', key: '', balance: '0.18'},
				{name: "Bibox",	api: '', key: '', balance: '0.18'},
				{name: "Binance",	api: '', key: '', balance: '0.18'},
				{name: "Bit-Z",	api: '', key: '', balance: '0.18'},
				{name: "Bitfinex",	api: '', key: '', balance: '0.18'},
				{name: "Poloniex",	api: '', key: '', balance: '0.18'}
			],
			apiStore: []
		}
	},
	methods: {
		selectDropItem(name) {
			this.dropList.select = name;
			this.visible.dropMenu = false;
		},
		pushApi(index) {
			this.apiStore.push(this.apiBox[index]);
			this.apiBox.splice(index, 1);
		},
		deleteApi(index) {
			this.apiStore[index].api = '';
			this.apiStore[index].key = '';
			this.apiBox.push(this.apiStore[index]);
			this.apiStore.splice(index, 1);
		}
	}
}
</script>
<style lang="scss" scoped>
.exchange {
	@include flex-col(stretch);
	align-items: stretch;
	padding: 0 13% 24px;
	&__box {
		@include flex-col-def(stretch);
		@extend %box;
		padding: 0;
		position: relative;
		z-index: 2;
		&--store {
			z-index: 0;
		}
		&--margin {
			margin-bottom: 16px;
		}
	}
	&__box-inner {
		@include flex-row(space-between);
		padding: 30px 32px;
	}
	&__title-wrapper {
		@include flex-row-def(center);
		padding: 30px 32px;
		&--avilible {
			padding: 0;
		}
	}
	&__title {
		margin-right: 17px;
	}
	&__header {
		padding: 5px 32px 20px;
		display: grid;
		grid-template-columns: 120px 8fr 3fr 3fr 2fr;
		justify-items: start;
		grid-column-gap: 24px;
		&--avilible {
			grid-template-columns: 120px auto;
		}
	}
	&__heading {
		font-size: $size-5;
		color: $middle-grey;
		font-weight: 600;
		letter-spacing: 0.35px;
		text-transform: uppercase;
		&--balance {
			justify-self: end;
		}
		&--status {
			margin-left: 4rem;
		}
	}
	&__question {
		@extend %question;
	}
	&__field {
		overflow-y: auto;
		flex: 1 1 512px;
	}
}
.api-item {
	padding: 25px 35px;
	display: grid;
	grid-template-columns: 120px 8fr 3fr 3fr 2fr;
	justify-items: start;
	grid-column-gap: 24px;
	align-items: center;
	font-size: $size-3;
	color: $text-active-color;
	font-weight: 600;
	border-top: 1px solid $line-color;
	&__name-wrapper {
		@include flex-row-def(center);
	}
	&__name-img {
		height: 24px;
		width: 24px;
		background-color: $text-hover-color;
		border-radius: 50%;
		margin-right: 1rem;
	}
	&__name {
		text-transform: capitalize;
	}
	&__balance {
		justify-self: end;
	}
	&__delete {
		background-color: transparent;
		width: 26px;
		height: 26px;
		@include flex-row(center);
		justify-self: end;
		transition: 0.2s ease-in-out;
		@media (min-width: 480px) {
			&:hover {
				opacity: 0.95;
			}
		}
	}
	&__delete-img {
		width: 18px;
		height: 18px;
		fill: $text-hover-color;
		pointer-events: none;
		transition: 0.2s ease-in-out;
	}
	&__status {
		width: 36px;
		margin-left: 4rem;
		position: relative;
		@include flex-row-def(center);
		z-index: 2;
		cursor: pointer;
		@media (min-width: 480px) {
			&:hover {
				filter: brightness(1.02);
			}
		}
	}
	&__status-input {
		left: 6px;
		opacity: 0;
		z-index: -1;
		position: absolute;
		pointer-events: none;
		&:checked + .api-item__status-dot-wrapper .api-item__status-dot {
			background-color: $text-hover-color;
			transform: translateX(95%);
		}
		&:checked + .api-item__status-dot-wrapper {
			background-color: rgba(0, 115, 255, 0.5);
		}
	}
	&__status-dot-wrapper {
		width: 34px;
		height: 14px;
		border-radius: 25px;
		background-color: rgba(208, 219, 230, 0.8);
		transition: 0.3s ease-in-out;
		@include flex-row-def(center);
	}
	&__status-dot {
		width: 20px;
		height: 20px;
		background-color: $light-grey;
		border-radius: 50%;
		transition: 0.3s ease-in-out;
		transform: translateX(-4px);
	}
}
.drop-menu {
	cursor: pointer;
	position: relative;
	width: 200px;
	height: 36px;
	@include flex-row-def(center);
	border: 1px solid $border-input;
	border-radius: 2px;
	padding: 0 12px;
	&:before {
		content: ''; 
		position: absolute; 
		top: 16px;
		right: 12px;
    border: 5px solid transparent; 
    border-top: 5px solid $triangle-color; 
	}
	&__placeholder {
		color: $light-grey;
		pointer-events: none;
	}
	&__list {
		left: -1px;
		right: -1px;
		top: calc(100% + 1px);
		position: absolute;
		border: 1px solid $border-input;
		border-top: none;
		max-height: calc(42px * 4);
		overflow-y: auto;
		background-color: $white;
	}
	&__item {
		cursor: pointer;
		height: 42px;
		@include flex-row-def(center);
		padding-left: 16px;
		transition: 0.1s ease-in-out;
		@media (min-width: 480px) {
			&:hover {
				background-color: $item-hover;
				.drop-menu__item-name {
					color: $text-hover-color;
				}
			}
		}
	}
	&__item-img {
		height: 24px;
		width: 24px;
		background-color: $text-hover-color;
		border-radius: 50%;
		margin-right: 1rem;
	}
	&__item-name {
		color: $semidark-grey;
		font-size: $size-3;
		font-weight: 600;
		transition: 0.1s ease-in-out;
	}
}
.api-box {
	padding: 20px 35px;
	display: grid;
	grid-template-columns: 120px 6fr 6fr 2fr auto 4fr;
	grid-column-gap: 24px;
	justify-items: start;
	align-items: center;
	font-size: $size-3;
	color: $text-active-color;
	font-weight: 600;
	border-top: 1px solid $line-color;
	&__input {
		height: 36px;
		justify-self: stretch;
		@include flex-row-def(center);
		border: 1px solid $border-input;
		border-radius: 2px;
		padding: 0 12px;
		color: $text-active-color;
		font-size: $size-3;
		font-weight: 600;
		transition: 0.1s ease-in-out;
		&::placeholder {
			color: $light-grey;
		}
	}
	&__name-wrapper {
		@include flex-row-def(center);
	}
	&__name-img {
		height: 24px;
		width: 24px;
		background-color: $text-hover-color;
		border-radius: 50%;
		margin-right: 1rem;
	}
	&__name {
		text-transform: capitalize;
	}
	&__button-save {
		@extend %button-buy;
		text-transform: capitalize;
		width: 76px;
		height: 36px;
	}
	&__question {
		width: 20px;
		height: 20px;
		fill: $lightest-grey;
		cursor: pointer;
		transition: fill ease-in-out 0.15s;
		background-color: transparent;
		@include flex-row(center);
		@media (min-width: 480px) {
			&:hover .api-box__question-img{
				fill: $light-grey;
			}
		}
	}
	&__question-img {
		width: 17px;
		height: 17px;
		fill: $lightest-grey;
		pointer-events: none;
		transition: fill ease-in-out 0.15s;
	}
	&__create-button {
		white-space: nowrap;
		background-color: transparent;
		color: $text-hover-color;
		font-size: $size-3;
		font-weight: 600;
		transition: fill ease-in-out 0.15s;
		@media (min-width: 480px) {
			&:hover {
				opacity: 0.95;
			}
		}
	}
}
</style>



